  /* ------------------ Landing page modifications ------------------ */
  #top-half-wrapper {
    width: 100%;
    height: 700px;
  }
  
  #landing-img{
    background-image: url("../assets/celebration.jpg");
    height: 100%;
    width: 100%;
    position: relative;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background: -webkit-gradient(
                linear, 
                left top, left bottom, 
                from(rgba(20,20,20, .5)), 
                to(rgba(20,20,20, .5))),
                url("../assets/celebration.jpg") no-repeat center;
    background: -webkit-linear-gradient(
                rgba(20,20,20, .5), 
                rgba(20,20,20, .5)),
                url("../assets/celebration.jpg") no-repeat center;
    background: -o-linear-gradient(
                rgba(20,20,20, .5), 
                rgba(20,20,20, .5)),
                url("../assets/celebration.jpg") no-repeat center;
    background: linear-gradient(
                rgba(20,20,20, .5), 
                rgba(20,20,20, .5)),
                url("../assets/celebration.jpg") no-repeat center;
    background-size: cover;
  }
  
